import { connect } from "react-redux";

function JobList({ jobs }) {
  return (
    <div>
      {jobs.map(job => {
        return <JobCard job={job} />;
      })}
    </div>
  );
}

function JobCard({ job }) {
  return (
    <div>
      <h1>{job.title}</h1>
    </div>
  );
}

function mapStateToProps(state) {
  const { jobs } = state;
  return { jobs };
}

export default connect(mapStateToProps)(JobList);
